@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@200;300;400;500&display=swap");
* {
  font-size: 2rem;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "IBM Plex Mono", monospace;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  background-color: #eee;
}

h4 {
  margin: 0;
  font-size: 1.5rem;
  vertical-align: middle;
}

span {
  display: inline-block;
  align-items: center;
  justify-content: center;
}

svg {
  width: 1.5rem;
}

.left {
  max-width: 35rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: 5rem;
  word-wrap: break-word;
}
.left__description {
  color: rgb(57, 104, 158);
}
.left__bottom {
  color: #ff5b5b;
}

.container {
  display: flex;
  align-items: stre;
  background-color: #fff;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  margin: 0rem auto;
  text-align: left;
  padding: 4rem;
  border: 2px solid #181818;
}

.calculator {
  text-align: center;
  flex-shrink: 0;
  background-color: rgb(57, 104, 158);
  box-shadow: 2px 2px;
  border-radius: 0rem;
  padding: 2rem 1rem;
  font-family: "IBM Plex Mono", monospace;
  border: 2px solid #181818;
}
.calculator__title {
  color: #fff;
}
.calculator .display__input {
  text-align: right;
  padding: 1.5rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  outline: none;
  border: 1px solid #181818;
  background-color: #fff;
  box-shadow: -1px -1px;
  border-radius: 2rem;
}
.calculator input[type=text][disabled] {
  color: #181818;
}
.calculator .keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(4rem, auto);
  gap: 0.8rem;
}
.calculator .keypad button {
  font-family: "IBM Plex Mono", monospace;
  border: 1px solid #181818;
  box-shadow: 2px 2px #181818;
  border-radius: 3rem;
  background-color: white;
  transition: transform 0.2s;
}
.calculator .keypad button:active {
  transform: scale(0.98);
}
.calculator .keypad button.highlight {
  color: #fff;
  font-weight: 500;
}
.calculator .keypad button.highlight--1 {
  background-color: #292929;
}
.calculator .keypad button.highlight--2 {
  color: #fff;
  background-color: #ff5b5b;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
}

#result {
  grid-column: 3/5;
  grid-row: 5;
}